.formTitle {
  text-align: center;
}

.search {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;

}

.form-group {
  display: -ms-flexbox;
  display: flex;
  margin: 1.2em 0 0.3em 0;
}

.form-group input {
  background-color: #303030;
  border: none;
  border-bottom: 2px solid #fff;
  outline: none;
  color: #fff;
  font-size: 1em;
}

.form-group label {
  margin: 0 0.7em 0 0.7em;
}

.button {
  margin: 2em 0 2em 0;
  border: 2px solid #fff;
  color: #fff;
  border-radius: 6px;
  padding: 0.7em 2em 0.7em 2em;
  background-color: #303030;
  outline: none;
}


